.product {
  --columns: 1;
  grid-template-columns: repeat(var(--columns), 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .product {
    --columns: 2;
  }
}

@media (width >= 768px) {
  .product {
    --columns: 3;
  }
}

@media (width >= 1024px) {
  .product {
    --columns: 4;
  }
}

.product-card {
  border: 1px solid #f0f1f4;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  transition: transform .3s;
  display: flex;
}

.product-card:hover {
  transform: scale(1.2);
}

.product-card__photo {
  width: 160px;
  height: 134px;
  margin: 0 auto;
  display: block;
}

.product-card__title {
  font-weight: 500;
  line-height: 18px;
  font-size: inherit;
  color: #060b35;
  margin: 40px 0 4px;
}

.product-card__title:hover {
  cursor: pointer;
  color: #34568b;
}

.product-card__review {
  justify-content: space-between;
  align-items: center;
  margin-top: 16px;
  display: flex;
}

.product-card__code {
  color: #616070;
  font-size: 10px;
  line-height: 14px;
}

.product-card__price-block {
  justify-content: space-between;
  align-items: center;
  height: 18px;
  margin: 24px 0 16px;
  display: flex;
}

.product-card__price-item {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.product-card__price-name {
  color: #616070;
  line-height: 18px;
}

.product-card__review-count {
  color: #060b35;
  height: 12px;
  font-size: 10px;
  line-height: 14px;
}

.product-card__button {
  color: #fff;
  text-align: center;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  outline: none;
  width: 166px;
  height: 40px;
  margin: 0 auto;
  padding: 12px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: block;
}

.product-card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: 50%;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars.stars--4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

.header {
  background: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
  padding: 23px 0;
  font-weight: 500;
  text-decoration: none;
  display: flex;
}

.nav__list {
  gap: 20px;
  height: 60px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.logo__img {
  padding: 10px 0;
  display: block;
}

.logo {
  margin: 0;
  padding: 0;
  line-height: 0;
}

a.nav__link:hover {
  color: #00acdc;
}

a.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.is-active {
  color: #00acdc;
  position: relative;
}

